/* vt323-400normal - all */
@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('VT323 Regular'),
    local('VT323-Regular'), 
    url('./files/vt323-all-400.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
    url('./files/vt323-all-400.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
